import React, {useEffect, useState} from 'react';
import {Card, Grid} from 'semantic-ui-react';

import {useSubstrate} from './substrate-lib';

function Main(props) {
    const {api} = useSubstrate();
    const {finalized} = props;
    const [header, setHeader] = useState(null);

    useEffect(() => {
        let unsubscribeAll = null;
        api.rpc.chain.subscribeNewHeads((head) => {
            setHeader(head);
        }).then(unsub => {
            unsubscribeAll = unsub;
        }).catch(console.error);

        return () => unsubscribeAll && unsubscribeAll();
    }, []);

    return (
        <Grid.Column>
            {header &&<Card fluid>
                <Card.Content textAlign='center'>
                    <Card.Header>{'blockNumber: ' + header.number.toNumber()}</Card.Header>
                    <Card.Meta>{'blockHash ' + header.hash.toHex()}</Card.Meta>
                </Card.Content>
                <Card.Content textAlign='center'>
                    {'parentHash ' + header.parentHash.toHuman() +'\n'}
                </Card.Content>
                <Card.Content textAlign='center'>
                    {'stateRoot ' + header.stateRoot.toHuman() +'\n'}
                </Card.Content>
                <Card.Content textAlign='center'>
                    {'extrinsicsRoot ' + header.extrinsicsRoot.toHuman() +'\n'}
                </Card.Content>
            </Card>}
        </Grid.Column>
    );
}

export default function Info(props) {
    const {api} = useSubstrate();
    return api.rpc &&
    api.rpc.chain &&
    api.rpc.chain.subscribeNewHeads  ? (
        <Main {...props} />
    ) : null;
}